import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import '../Components/registration.scss'


export default function Register() {
  const [fullName, setFullName] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [passwordError, setPasswordError] = useState("");
  const [email, setEmail] = useState("");
  const [emailError, setEmailError] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [phoneNumberError, setPhoneNumberError] = useState("");

  const history = useNavigate()
  const handleSubmit = async (e) => {
    // prevent the form from refreshing the whole page
    e.preventDefault();

//Clear Error message when validation is passed
    var clear_emailError = document.querySelector('#email');
    clear_emailError.addEventListener('keypress',clearEmailError)
    function clearEmailError(event){
      var email__error = document.querySelector('#email_error');
      email__error.style.display = 'none';
    }

    var clear_passwordError = document.querySelector('#password');
    clear_passwordError.addEventListener('keypress',clearpasswordError)
    function clearpasswordError(event){
      var password__error = document.querySelector('#password_error');
      password__error.style.display = 'none';
    }
    var clear_phoneNumber_error = document.querySelector('#phoneNumber');
    clear_phoneNumber_error.addEventListener('keypress',clearPhoneNumberError)
    function clearPhoneNumberError(event){
      var phoneNumber__error = document.querySelector('#phoneNum_error');
      phoneNumber__error.style.display = 'none';
    }
    //Set validation response message
    const pasErr = "Your password did Not Match!"
    const emailErr = "Your Email is Invalid!"
    const phoneNumErr = "Your Phone Number Must not be more than 13 digits and should follow the order as shown!"

    //Emaill validation
const validateEmail = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/
if (!validateEmail.test(email)){
  return  setEmailError(emailErr)
}

     //Password validation
     if(password !== confirmPassword){
      return setPasswordError(pasErr)
    }

    // Phone number validation 
  var phoneNo = /^\+\d{3}-\d{3}-\d{3}-\d{4}$/
  if((!phoneNumber.match(phoneNo))){
    return setPhoneNumberError(phoneNumErr)
        }else{ 
          localStorage.setItem('fullname',JSON.stringify(fullName))
          history('/dashboard') 
        }

  
  }

  return (
    
    <React.Fragment>
      <div className='registration'>
        <div className='image__wrapper'>
        <img src='./image/page_image.JPG' alt='registeration_image'/>
        <span className='image__wrapper--description'>Choose a date range</span>
        <p className='image__wrapper--text'>Any date convenient for you; is convenient for us </p>
      </div>
    <div className="registration__form">

      <div className="form__header">
        <h2 className="form__header--text">Create an Account</h2>
      </div>
     
      <form encType="multipart/form-data"  method="post">
        
      <div className="form__group">
          <div className="input__wrapper">
          <label htmlFor="confirmpassword" className="text__label">Your Email Address</label>
            <input
              type="email"
              name="email"
              autoComplete="none"
              id="email"
              placeholder="Email"
              required
              className="form__control"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              />
            
          </div>
          <div className="help_parent">
            <span id='email_error' className="help__block">{emailError}</span>
            </div>
        </div>

      

        <div className="form__group">
          <div className="input__wrapper">
          <label htmlFor="password" className="text__label">Your Password</label>
            <input
              type="password"
              id="password"
              name="password"
              required
              autoComplete="none"
              placeholder="Password"
              className="form__control"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              />
            
          </div>
          <div className="help_parent">
            <span id='password_error' className="help__block">{passwordError}</span>
            </div>
        </div>

        <div className="form__group">
          <div className="input__wrapper">
          <label htmlFor="confirmPassword" className="text__label">Confirm Your Password</label>
            <input
              type="password"
              id="confirmPassword"
              name="confirmPassword"
              autoComplete="none"
              placeholder="Confirm Password"
              className="form__control"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
               />
            
          </div>
        </div>

        <div className="form__group">
          <div className="input__wrapper">
          <label htmlFor="fullName" className="text__label"> Your Full Name</label>
            <input
              type="text"
              id="fullName"
              name="fullName"
              required
              placeholder="full Name"
              className="form__control"
              value={fullName}
              onChange={(e) => setFullName(e.target.value)} 
              />
            
          </div>
        </div>


        <div className="form__group">
          <div className="input__wrapper">
          <label htmlFor="phoneNumber" className="text__label">Your Phone Number</label>
            <input
              type="text"
              name="phoneNumber"
              id="phoneNumber"
              required
              placeholder=" format => +234-803-771-4029"
              className="form__control"
              value={phoneNumber}
              onChange={(e) => setPhoneNumber(e.target.value)}
              />
           
          </div>
          <div className="help_parent">
            <span id='phoneNum_error' className="help__block">{phoneNumberError}</span></div>
        </div>

        <div className="form__group">
          <div className="input_parent">
         
            <input
              type="radio"
              name="agreement"
              className="agreement"
              id='agreement'
              required
              />
            <label htmlFor="agreement" className="label__agree">I read and Agree with Terms and Conditions</label>
          </div>
         
        </div>


        <div className="form__submit">
            <input 
            type="submit" 
            className=" form__submit form__submit--btn" 
            onClick={(e) => handleSubmit(e)} 
            value="Create Account" />
        </div>
      
      </form>
      <div>
    
      </div>
    </div>
    </div>
    </React.Fragment>
  )
}
