import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import BarChart from '../Components/BarChart';
import '../Components/dashboard.scss'
import Icon from "../Icon";

export default function Dashboard() {
const [fullName, setFullName] = useState('')

const history = useNavigate()



var days_random_Numb1 =  Math.floor((Math.random() * 100) + 1)
var days_random_Numb2 =  Math.floor((Math.random() * 100) + 1)
var days_random_Numb3 =  Math.floor((Math.random() * 100) + 1)
var days_random_Numb4 =  Math.floor((Math.random() * 100) + 1)
var days_random_Numb5 =  Math.floor((Math.random() * 100) + 1)
var days_random_Numb6 =  Math.floor((Math.random() * 100) + 1)
var days_random_Numb7 =  Math.floor((Math.random() * 100) + 1)
var days_random_Numb8 =  Math.floor((Math.random() * 100) + 1)
var days_random_Numb9 =  Math.floor((Math.random() * 100) + 1)
var days_random_Numb10 =  Math.floor((Math.random() * 100) + 1)

const data = [
  {week: 1, days: days_random_Numb1},
  {week: 2,  days: days_random_Numb2},
  {week: 3,  days: days_random_Numb3},
  {week: 3,  days: days_random_Numb4},
  {week: 4,  days: days_random_Numb5},
  {week: 5, days: days_random_Numb6},
  {week: 6,  days: days_random_Numb7},
  {week: 7,  days: days_random_Numb8},
  {week: 8,  days: days_random_Numb9},
  {week: 9,  days: days_random_Numb10},
  {week: 10,  days: days_random_Numb10},

]


useEffect(() => {
const greeting = JSON.parse(localStorage.getItem('fullname'))
if(greeting){
  setFullName(greeting)
}else{
  history('/')
}

},[fullName,history]);

const Logout =  () => {
      localStorage.removeItem('fullname')
      history("/");
}

  return (
    <div className='chart__container'>
      <div className='welcome__greeting'>
       <h1>Welcome: {fullName} </h1> 
       <button onClick={Logout} className='logout'>Logout</button>
      </div>
       
      <div className='chart__wrapper'>
      <div className='calender'>
        <Icon icon="calendar" size={60} color="blue" />
       
      </div>
      <div className='menu'>
        week to date
        <Icon icon="cheveron-down" size={20} color="black" />
      </div>
      <BarChart data={data} />
      </div>
    </div>
  )
}
